





























import Vue from "vue";
import { routeEvents, IRouteSubmit } from "../bus/RouteEvents";
import {
    coordinates,
    Coordinates,
    TripAdvisor,
    ITripStatus,
    routeCalculator,
    Hop,
    validHops,
    Platform,
    CancelledError,
    Explanation,
    ILegOfJourney,
} from "common";

export default Vue.extend({
    data(): { start: Coordinates | null; dest: Coordinates | null; ta: TripAdvisor | null; journey: Explanation | null } {
        console.log("getting DATA!!!!!!!!!!!");
        return {
            start: null,
            dest: null,
            ta: null,
            journey: null,
        };
    },
    methods: {
        onRouteSubmit(event: IRouteSubmit) {
            console.log("on route submit");
            this.start = event.start;
            this.dest = event.dest;
        },

        isOdd(v: number): boolean {
            return v % 2 !== 0;
        },

        async calculateTrip() {
            try {
                if (this.ta !== null) {
                    if (!this.ta.hasRoute) {
                        this.ta!.status.cancelled = true;
                    }
                    this.ta = null;
                    this.$forceUpdate();
                }

                console.log("STARTING CALCULATE TRIP");
                const galaxy = "01 Euclid";

                const allHops: Hop[] = validHops()
                    .filter(hop => hop.platform === Platform.PC || hop.platform === Platform.XBOX)
                    .filter(hop => hop.galaxy === galaxy);

                const status: ITripStatus = { cancelled: false, tries: 0 };
                this.ta = new TripAdvisor(
                    routeCalculator(allHops, 2000, 0.93),
                    { label: "start", coords: this.start! },
                    { label: "destination", coords: this.dest! },
                    status
                );
                console.log("before");
                const r = await this.ta!.route();
                console.log("after");
                console.log(`--> ${this.ta.hasRoute}`);
                //this.$forceUpdate();

                this.journey = await this.ta.explanation();

                for (const leg of this.journey.legs()) {
                    console.log(leg.description);
                }

                console.log("final");
            } catch (e) {
                if (!(e instanceof CancelledError)) {
                    console.log(e);
                }
            }
        },
    },
    watch: {
        start() {
            this.calculateTrip();
        },
        dest() {
            this.calculateTrip();
        },
    },
    mounted() {
        console.log("MOUNTED");
        routeEvents.listenRouteSubmit(this.onRouteSubmit);
    },
});
