















































import Vue from "vue";
import { coordinates } from "../../../common/lib/browser";
import { routeEvents } from "../bus/RouteEvents";

export default Vue.extend({
    data() {
        return {
            coordPattern: "0?[0-9a-fA-F]{1,3}:0?0?[0-9a-fA-F]{1,2}:0?[0-9a-fA-F]{1,3}:0?[0-2]?0?[0-9a-fA-F]{1,3}",
            startVal: "",
            destVal: "",
        };
    },
    methods: {
        submitForm() {
            console.log(`SUBMITTING: ${this.startVal}, ${this.destVal}`);
            routeEvents.raiseRouteSubmit({
                start: coordinates(this.startVal),
                dest: coordinates(this.destVal),
            });
        },
    },
});
